@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&family=Roboto+Mono&display=swap);
:root{--blue: #17cfe7}*{margin:0;padding:0;box-sizing:border-box}body{margin:0;font-family:"Poppins",sans-serif;font-family:"Roboto Mono",monospace;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#000;color:#fff}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}


.dashboard{display:flex;justify-content:space-between;align-items:flex-start;transition:all .4s;text-align:center}.dashboard form{width:100%;height:100vh;display:flex;justify-content:center;align-items:center;text-align:center;flex-direction:column}.dashboard form div input{height:40px;width:300px;background:none;border:none;border-radius:30px;border:3px solid var(--blue);color:#fff;padding:0 20px 0 20px;outline:none;font-size:18px}.dashboard form div button{width:100px;height:40px;background:none;border:3px solid var(--blue);color:#fff;border-radius:30px;padding:5px;font-size:18px;margin-left:15px;transition:all .3s}.dashboard form div button:hover{background-color:var(--blue)}.dashboard form h1{margin-top:20px;font-size:18px;border-bottom:2px solid var(--blue);padding-bottom:5px}.dashboard video{width:60vw;height:auto;margin:20px 0 0 0;border:2px solid var(--blue)}.dashboard .wrapper{width:100%;display:flex;flex-direction:column;justify-content:start;align-items:center;padding:10px !important}.dashboard .wrapper .wrapper_1{width:100%}.dashboard .wrapper .wrapper_1 h1{text-align:center;width:100%}.dashboard .wrapper .wrapper_1 button{background-color:#1ba5cf;border:none;height:30px;font-size:15px;color:#fff;margin:10px;padding:0 10px 0 10px;border-radius:10px;text-align:center;transition:all .2s}.dashboard .wrapper .wrapper_1 button:hover{background-color:#117999}.dashboard div{width:100%}.dashboard div .option{width:100%}.dashboard div .option h4{text-align:center;width:100%;border:2px solid var(--blue);padding:10px;border-radius:20px;margin-bottom:20px}.dashboard div .option ol{width:100%;border:2px solid var(--blue);padding:10px;border-radius:20px;text-align:start;list-style-position:inside;list-style-type:none;color:var(--blue)}.dashboard div .option ol li{width:100%;margin-top:10px;margin-bottom:20px}.dashboard div .option ol li button{width:80%;color:#fff;background-color:#007a8a;border:none;padding:10px;border-radius:20px;transition:all .2s;cursor:pointer}.dashboard div .option ol li .mp4{background-color:#57009e}.dashboard div .option ol li .mp4:hover{background-color:#7a00df}.dashboard div .option ol li button:hover{background-color:#00a0d1}.dashboard div .option ol li span{margin-left:10px;color:#fff;padding:5px 10px 5px 10px;border-radius:20px;cursor:pointer;height:30px;width:30px;border:2px solid var(--blue);transition:all .2s}.videoH1{border:2px solid var(--blue);border-radius:20px;margin-top:20px;padding:10px}
